.navbar-container {
  height: 70px;
  width: 100%;
  top: 0;
  display: flex;
  align-items: center;
  position: fixed;
  z-index: 1;
  background: transparent;
}
.navbar-wrapper {
  margin: 0 auto;
  max-width: 85%;
  width: 85%;
  display: flex;
  justify-content: space-between;
}
.nav-list {
  display: flex;
  width: 70%;
  justify-content: space-evenly;
  align-items: center;
}

.nav-logo {
  width: fit-content;
  height: 24px;
}
.nav-logo img {
  width: 100%;
  height: 100%;
}
