* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
  scroll-behavior: smooth-scroll;
}
.container {
  min-height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.background {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 50% 50%;
  background-size: cover;
}
.dark-overlay {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Montserrat", sans-serif;
}

.caption {
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
}
.caption-s {
  font-size: 15px;
  font-weight: 500;
}
.caption-pink {
  color: #ff214f;
}
.desc {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: #828282;
  font-weight: 400;
  line-height: 24px;
  max-width: 480px;
}

.header {
  font-weight: 600;
  color: #232323;
  font-size: 32px;
  max-width: 500px;
  line-height: 1.2;
  margin: 20px 0px;
}
.medheader {
  font-size: 20px;
  color: #232323;
  font-weight: 500;
  line-height: 24px;
  margin: 20px 0px;
}

.subheader {
  font-size: 14px;
  color: #232323;
  font-weight: 600;
  line-height: 24px;
  margin: 20px 0px;
}
.btn {
  padding: 10px 20px;
  font-weight: 600;
  letter-spacing: 1px;
  font-size: 11px;
  font-family: "Montserrat", sans-serif;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
  cursor: pointer;
}
.btn-border {
  background: transparent;
  border: 2px solid white;
  color: rgb(255, 255, 255);
  transition: 0.3s;
  margin: 20px 0px;
}
.btn-border:hover {
  background: white;
  color: black;
}
.btn-colored-light {
  background: #333;
  border: 2px solid transparent;
  color: white;
  margin: 20px 0px;
}
.btn-colored-light:hover {
  background: white;
  color: black;
  border: 2px solid #333;
}
.btn-colored-dark {
  background: rgb(255, 255, 255);
  border: 2px solid transparent;
  color: rgb(0, 0, 0);
  margin: 20px 0px;
}
.btn-colored-dark:hover {
  background: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  border: 2px solid rgb(255, 255, 255);
}
.btn svg {
  margin-right: 10px;
}
.link {
  color: white;
  text-decoration: none;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: -1;
}
.dark {
  color: white;
}
@media only screen and (max-width: 900px) {
  .container {
    padding: 20px;
  }
  .header {
    font-size: 20px;
  }
}


