.card-wrapper {
  border: 1px solid rgb(245, 245, 245);
  height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 50px;
  background-color: white;
  box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
}
.client-img {
  width: 80px;
  height: 80px;
  border-radius: 80px;
  overflow: hidden;
}

.client-img img {
  width: 100%;
  height: 100%;
}
.client-card .caption {
  margin: 10px 0px 5px;
  color: rgb(23, 23, 23);
  font-weight: 600;
}

.client-card .desc-small {
  font-size: 11px;
  font-weight: 500;
  text-transform: uppercase;
}

.blogs-container .header {
  font-weight: 700;
  border-bottom: 1px solid #ff214f;
  padding-bottom: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.blogs-container .caption {
  font-size: 14px;
  padding: 0px;
  margin: -10px 0px;
  color: #aaa;
}

.blog-list {
  display: flex;
  max-width: 76%;
  grid-gap: 30px;
  gap: 30px;
  margin-top: 60px;
}
@media only screen and (max-width: 900px) {
  .blog-list {
    flex-direction: column;
    max-width: 100%;
  }
}

.blog-card {
  width: 24%;
  overflow: hidden;
}
.blog-img {
  height: auto;
  overflow: hidden;
}
.blog-img:hover img {
  opacity: 0.6;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
}
.blog-img img {
  transition: 0.3s;
  width: 100%;
  height: 100%;
}
.blog-about .desc-small {
  color: #939393;
  font-size: 11px;
  margin-top: 20px;
  cursor: pointer;
}
.blog-about .desc-title {
  color: #232323;
  font-size: 16px;
  line-height: 23px;
  cursor: pointer;
  transition: 0.2s;
}
.blog-about .desc-content {
  margin-top: 20px;
  border-top: 1px solid #dadada;
  padding-top: 20px;
  cursor: default;
}
.blog-about .desc-title:hover,
.blog-about .desc-small:hover {
  color: #ff214f;
}
@media only screen and (max-width: 900px) {
  .blog-card {
    width: 100%;
  }
  .blog-about {
    text-align: center;
  }
}

.base-container {
  min-height: 15vh;
  background-color: #141414;
  flex-direction: row;
}

.logo {
  width: 200px;
  height: 24px;
}
.logo img {
  width: 100%;
  height: 100%;
}
.base-container .caption-s {
  color: #969696;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.base-container .caption-s svg {
  color: white;
  margin: 0px 10px;
  font-size: 18px;
}
@media only screen and (max-width: 900px) {
  .logo {
    width: 100px;
    height: 12px;
  }
  .logo img {
    width: 100%;
    height: 100%;
  }
  .base-container .caption-s {
    font-size: 10px;
  }
  .base-container .caption-s svg {
    margin: 0px 4px;
  }
}

